



















import {Vue, Component, Watch} from 'vue-property-decorator'

@Component({
    components: {

    }
  }
)
export default class Table extends Vue {

  private show_chart:boolean = true
  private btc_price:any = ''
  private price_socket:any = null
  created(): void {

  }

  mounted(): void {
    this.init_table()
    this.get_price()
  }

  init_table(){
    // @ts-ignore
    new TradingView.widget(
      {
        autosize:true,
        "symbol": "HUOBI:BTCUSDT",
        "interval": "W",
        "timezone": "Asia/Shanghai",
        "theme": "light",
        "style": "1",
        "locale": "zh_CN",
        "toolbar_bg": "#f1f3f6",
        "enable_publishing": false,
        "allow_symbol_change": true,
        "details": true,
        "studies": [
          "MASimple@tv-basicstudies"
        ],
        "container_id": "tradingview_b0468"
      }
    );
    this.show_chart = false
  }
  get_price(){
    this.price_socket = new WebSocket('wss://ws.coincap.io/prices?assets=bitcoin')
    this.price_socket.onmessage = (e: { data: string; }) => {
      let price = JSON.parse(e.data)
      this.btc_price = Number(price.bitcoin).toFixed(2)
    }
  }

  beforeDestroy(): void {
    this.price_socket.close()
  }

}
